
.rc-steps-item-process .rc-steps-item-icon {
    background: $color-primary;
    border-color: $color-primary;
}

.rc-steps-item-wait .rc-steps-item-icon {
    border-color: $color-primary;
    //background: $color-grey-dark-4;
    background: $color-primary;
    opacity: .35;
}

.rc-steps-item-finish .rc-steps-item-icon {
    border-color: $color-primary;
    background: $color-primary;
}

.rc-steps-item-icon > .rc-steps-icon {
    color: $color-white !important;
}

.rc-steps-item .rc-steps-item-title:after {
    background-color: $color-primary !important;
}

.rc-steps-item-wait .rc-steps-item-title:after,
.rc-steps-item-process .rc-steps-item-title:after {
    background-color: $color-primary-darker !important;
    
}

.rc-steps-item-title:after {
    top: 50%;
}


.asteps {
    display: grid;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: $font-secondary;
    
    padding-top: 1rem;

    .steps-indicator {
        //width: 0;
        flex: 1;

        &:last-child {
            flex: 0;
        }

        display: flex;
        gap: .5rem;
        align-items: center;
        position: relative;
        
        &::before {
            border: 1px solid $color-primary;
            height: 1px;
            width: 100%;
            left: 0;
            top: 1rem;
            content: "";
            position: absolute;
        }

        &:last-child .steps-indicator-content {
            right: 100%;
        }

        &--done .steps-indicator-index {
            
        }

        &--selected .steps-indicator-index {
            background: $color-primary;
        }
    }

    .steps-indicator-index {
        border-radius: 50%;        
        width: 2rem;
        height: 2rem;
        padding-top:.2rem;
        //border: .4rem solid $color-primary;
        background: $color-grey-dark-4;
        border: .4rem solid $color-grey-dark-2 !important;
        color: $color-primary;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 1;
    }
 

    .steps-indicator-content {
        display: inline-block;
        position: absolute;
        bottom: -100%;
        width: 100%;


        .steps-indicator-label {
            display: inline-block;
            padding-right: 1rem;
            position: relative;
            white-space: nowrap;
        }
    }

    
}