.list-market-modal {

    .error-text {
        color: $color-danger;
        overflow-wrap: break-word;
    }
    
    .action-description {
        max-width: 50rem;
        font-family: $font-secondary;
        font-size: $font-size-xsmall;
        color: $color-grey-light-2;
    }

    .price-input-container {
        display: flex;
        align-items: center;

        .price-input {
            max-width: 10rem;
            margin-right: .5rem;
            
            input { 
                text-align: right; 
            }
        }
    }
    
}
