.main {
    padding-top: 3rem;
    min-height: calc(100vh - 100rem);
    margin: auto;

    @include media(big-desktop) {
        max-width: $app-max-width-big-desktop;
    }
}

.page {
    max-width: $app-max-width;
    padding: $app-padding;
    margin: auto;

    @include media(phone) {
        padding: $app-padding-phone;
    }
}

.section-fw {
    &:nth-of-type(even) {
        background: $color-grey-dark-4;
        background: linear-gradient(134deg, lighten($color-grey-dark-4, 5%), $color-grey-dark-4);
        border-top: 1px solid lighten($color-grey-dark-4, 5%) !important;
    }

    .section-contents {
        max-width: $app-max-width;
        padding: $app-padding;
        margin: auto;       


        @include media(phone) {
            padding: $app-padding-phone;
        }
    }

}
