@keyframes punk-speech-bubble {
    0% {
        opacity: 0;
        transform: scale(0, 0);
        margin-top: 5rem;
    }

    5% {
        opacity: .9;
        transform: scale(1, 1);
        margin-top: 0rem;
    }
    
    25% {
        opacity: .9;
        transform: scale(1, 1);
        margin-top: 0rem;
    }

    30% {
        opacity: 0;
        transform: scale(0, 0);
        margin-top: 5rem;
    }

    100% {
        opacity: 0;
    }
}

.speech-bubble {

    position: absolute;
   // top: 1rem;
    z-index: 1;
    //width: fit-content;
    width: 14%;
    opacity: 0;


    border: .4rem solid black;
    color: #333;
    background: $color-white;
    border-radius: 3rem;
    padding: 1rem;


    font-size: 1.4rem;

    @include media(phone) {
        font-size: 1.9vw;
        width: 20vw;
        padding: .6rem;
    }

    &::before {
        content: "";
        position: absolute;
        bottom: -1.3rem;
        left: 40%;
        border-width: 1.2rem 1.2rem 0;
        border-style: solid;
        border-color: $color-black transparent;
        display: block;
        width: 0;
    }

    &::after {
        content: "";
        position: absolute;
        bottom: -.75rem;
        left: calc(40% + 0.2rem);
        border-width: 1rem 1rem 0;
        border-style: solid;
        border-color: $color-white transparent;
        display: block;
        width: 0;
    }



    &--1 {
        left: calc(100vw * 1 / 7 + 2rem);
        top: 1rem;
        animation: punk-speech-bubble 12s ease;
        animation-delay: 8s;
        animation-iteration-count: infinite;
    }

    &--3 {
        left: calc(100vw * 3 / 7 + 2rem);
        top: -1rem;
        animation: punk-speech-bubble 12s ease;
        animation-delay: 5s;
        animation-iteration-count: infinite;
    }
    
    &--5 {
        left: calc(100vw * 5 / 7 + 2rem);
        top: -3rem;
        animation: punk-speech-bubble 13s ease;
        animation-iteration-count: infinite;
        animation-delay: 2s;
    }

    &--punk-hover {
        opacity: 1;
        top: -3rem;
        left: 3rem;
    }

    animation-iteration-count: infinite;

}
