.page-punk-main {
    display: flex;
    gap: 3rem;

    @include media(phone) {
        flex-direction: column;
    }

    .error-text {
        margin-top: .5rem;
        color: $color-danger;
    }

    .attributes {
        display: flex;
        flex-wrap: wrap;
    }

    .attribute-box {
        display: flex;
        flex-direction: column;
        //border: 1px solid $color-primary;
        border-radius: $border-radius-small;
        padding: 1rem;
        margin: .5rem;

        &--type {
         //   border: 1px solid $color-primary-darker;
            .attribute-box-type {
                color: $color-primary-dark;
            }
            .attribute-box-value {
                color: $color-primary;
            }
        }
    }

    .attribute-box-type {
        color: $color-grey-dark-1;
        font-size: $font-size-small;
    }

    .attribute-box-rarity {
        color: $color-white;
        font-size: $font-size-small;
    }
}

.page-punk-column-left {
    justify-content: center;
    align-items: flex-start;
    display: flex;

    .punk-details-image-container {
        position: relative;

        .punk-image {
            border-radius: $border-radius-small;
            width: 40rem;
            max-width: 100vw;
    
            @include media(phone) {
                
            }
        }
    
        .punk-details-card-rarity {
            position: absolute;
            top: .3rem;
            left: .3rem;
            max-width: 40%;
            max-height: 3.2rem;
        }

        @keyframes blink-animation {
            50% {
                color: $color-primary;
            }
        }
          
        .punk-card-attrcount {
            position: absolute;
            top: .2rem;
            right: .4rem;
            color: $color-white;
            font-weight: bold;
            text-transform: uppercase;
            font-size: x-small;
            background: rgba(0,0,0,.5);
            padding: 6px;
            border-radius: 6px;
            animation: blink-animation 2s infinite;
        }
    
    }
}

.page-punk-column-right {
    display: flex;
    flex-direction: column;   
}

.page-punk-trade-history {
    margin-top: 3rem;
}
