.top-bar {
    display: flex;
    align-items: center;
    width: 100%;
    
    //max-width: $app-max-width;
    padding: $app-padding;
    background: $color-grey-dark-4;
    box-shadow: $shadow-below;
    padding-top: 2rem;
    padding-bottom: 2rem;

    margin: auto;

    @include media(phone) {
        padding: $app-padding-phone;
    }

    @include media(big-desktop) {
        max-width: $app-max-width-big-desktop;
        margin: auto; 
    } 
}

#shitpunks-brand {
    display: flex;
    align-items: center;

    #shitpunks-logo {
        height: 4rem;
    }
    
    #shitpunks-title {
        font-size: $font-size-xlarge;
        font-family: $font-secondary;
        font-weight: 600;
        margin-left: .5rem;
        color: $color-white-bright;

        span {
            color: $color-primary;
        }
        
        a {
            color: inherit;
            text-decoration: none;
        }
    }
}

.main-menu {
    display: flex;
    flex-grow: 1;
    justify-content: flex-end;
    margin-left: 2rem;
    margin-right: 2rem;
}

.main-menu-link {
    position: relative;
    font-family: $font-secondary;
    font-size: $font-size-medium;
    font-weight: 500;
    text-transform: uppercase;
    text-decoration: none;
    color: $color-grey-dark-1;
    padding: 1rem;
    white-space: nowrap;

    &:hover {
        text-decoration: none;
    }

    &--active {
        color: $color-white-bright;
    }

    &::before {
        position: absolute;
        content: "";
        width: 0;
        height: .2rem;
        background: $color-primary;
        left: 50%;
        bottom: 0;
        -webkit-transform: translateX(-50%);
        transform: translateX(-50%);
        border-radius: 10px;
        transition: all .2s ease;
        opacity: 0;
    }

    &:hover {
        color: $color-grey-light-1;

        &::before {
            width: calc(100% - 2rem);
            opacity: 1;
        }
    }
}

@include media(phone) {
    .top-bar {
        display: grid;
        grid-template-columns: 1fr auto;
    }

    #shitpunks-brand {
        grid-row: 1/1;
        grid-column: 1/1;
    }
    
    #wallet {
        grid-row: 1/1;
        grid-column: 2/3;
    }
    
    .main-menu {
        grid-row: 2/2;
        grid-column: 1/3;
        justify-content: center;
        margin-top: 1rem;
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        margin-left: 0;
        margin-right: 0;
    }

    .main-menu-link {
        font-size: $font-size-small;
        padding: .5rem;
    }
}



