.punk-card {
    position: relative;
    background: $color-black;
    display: flex;
    flex-direction: column;
    color: $color-white;
    //height: max-content;
    height: 100%;
    overflow: hidden;
}

.punk-image-container {
    position: relative;
    width: 100%;
    height: 0; 
    padding-top: 100%;
    background: $color-black;
    display: flex;
    animation: fadeIn 1s;
    
    cursor: pointer;

    .punk-image-wrapper, .spinner {
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
    }

    .punk-image {
        position: absolute;
        top: 0;
        max-width: 100%;
        border-top-left-radius: $border-radius-mini;
        border-top-right-radius: $border-radius-mini;
    }

    .punk-card-rarity {
        position: absolute;
        top: .2rem;
        left: .2rem;
        width: 12rem;
        max-width: 50%;
    }

    .punk-card-marketplace-image {
        height: 2rem;
        position: absolute;
        bottom: .5rem;
        right: .5rem;
    }

    &:hover {
        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(#fff, .2);
        }

    }

    @keyframes blink-animation {
        50% {
            color: $color-primary;
        }
    }
      
    .punk-card-attrcount {
        position: absolute;
        top: .2rem;
        right: .4rem;
        color: $color-white;
        font-weight: bold;
        text-transform: uppercase;
        font-size: x-small;
        background: rgba(0,0,0,.5);
        padding: 6px;
        border-radius: 6px;
        animation: blink-animation 2s infinite;
    }

    :hover ~ .punk-name {
        color: red !important;
    }
}




.punk-details {
    background-color: $color-white;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    border-bottom-left-radius: $border-radius-mini;
    border-bottom-right-radius: $border-radius-mini;
    color: $color-black;
    padding-bottom: .5rem;
}

.punk-name {
    background: $color-primary;
    padding: .2rem;
    text-align: center;
    white-space: nowrap;
}

.punk-statrow {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    padding: .6rem;
}

.punk-stat {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    font-family: Inter;
    padding-top: .4rem;
    padding-bottom: .4rem;
    max-width: 7rem;
    overflow: hidden;

    .bnb-icon {
        height: 1.6rem;
        margin-right: .2rem;
    }
}

.punk-stat-title {
    color: #666;
    font-size: $font-size-xsmall;
    overflow: hidden;
    padding-bottom: .4rem;
    text-align: center;
    width: 100%;
}

.punk-stat-value {
    font-size: $font-size-small;
    font-weight: 600;
    display: flex;
    align-items: center;
    overflow: hidden;
}

.punk-timestamp {
    font-size: $font-size-xsmall;
    padding: 0rem 0.5rem;
    margin-bottom: 1rem;
    text-align: center;

    span {
        &.increased {
            color: $color-green;
        }

        &.reduced {
            color: $color-danger;
        }
    }
}


.punk-card-front {
    backface-visibility: hidden;

    transform: rotateY(180deg);

    &--visible {
        transform: rotateY(0deg);
    }
}

.punk-card-side {
    backface-visibility: hidden;
    transition: transform .3s ease;
}

.punk-card-back {
    z-index: -1;
    position: absolute;
    //background: darken($color-white, 10%);
    background: linear-gradient(358deg, #f88514, #e53701);
    background: $color-grey-dark-4;

    color: $color-white;
    top: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;

    transform: rotateY(-180deg);
    
    border-radius: $border-radius-mini;
    padding: 1rem;

    font-size: $font-size-small;
    font-family: $font-secondary;

    a {
        color: $color-white;
    }

    &--visible {
        z-index: 0;
        transform: rotateY(0deg);
        pointer-events: initial;
    }

    .punk-card-back-item-label {
        font-variant: all-small-caps;
        color: $color-grey-dark-3;
        color: $color-primary;
        margin-bottom: .2rem;
    }

    .punk-card-back-attribute-list {
        display: flex;
        flex-wrap: wrap;
        gap: .3rem;
    }

    .punk-card-back-attribute-item {
        cursor: pointer;
        display: flex;
        flex-direction: column;
        font-size: $font-size-xsmall;
        padding: .1rem .5rem;
        background: $color-grey-dark-3;
        border-radius: $border-radius-small;
        box-shadow: $shadow-below;

        .punk-card-back-trait-type {
            font-size: $font-size-xsmall;
            color: $color-grey-dark-1;
            white-space: nowrap;
        }

        .punk-card-back-trait-value {
            color: $color-white;
        }

        border: 1px solid transparent;

        &--selected {
            border: 1px solid #ff7a01;
        }

        &:hover {
            background: lighten($color-grey-dark-3, 10%);
        }
    }


    .image-and-seller {
        display: flex;
        gap: 1rem;
        justify-content: flex-start;
        align-items: flex-start;
        margin-bottom: 1rem;
    }
    .punk-image-container {
        width: 30% !important;
        padding-top: 30%;

        img {
            border-radius: $border-radius-small;
        }

        border-radius: $border-radius-small;
    }
}

.dogear {
    position: absolute;
    bottom: 0;
    right: 0;

    width: 3rem;
    height: 3rem;

    cursor: pointer;

    &:hover {
        .triangle-bottomright {
            border-bottom-width: 2rem;
            border-left-width: 2rem;
    
            box-shadow: -2px -2px 3px 2px rgba(0,0,0,0.35);
        }
    }
}

.triangle-bottomright {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 0;
    height: 0;
    border-bottom: 1.5rem solid $color-black;
    border-left: 1.5rem solid darken($color-white, 20%);
    
    
    box-shadow: -1px -1px 2px 1px rgba(0,0,0,0.5);

    &--back {
        border-bottom: 1.5rem solid $color-black;
        border-left: 1.5rem solid darken($color-white, 20%);
        box-shadow: -1px -1px 3px 2px rgba(0,0,0,0.3);
    }

    transition: all .1s ease;

    &:hover {

    }

  }