.footer {
    padding: 3rem;
    background: $color-grey-dark-4;
    min-height: 20rem;

    @include media(big-desktop) {
        max-width: $app-max-width-big-desktop;
        margin: auto; 
    } 

    .footer-contract {
        white-space: nowrap;


    position: relative;
        .copied-text {
            position: absolute;
            margin-left: 1rem;
        }
    }

    .copy-clipboard-icon {
        color: $color-primary;
        cursor: pointer;

        &:hover {
            color: $color-white;
        }
    }

}

.footer-items {
    display: flex;
    justify-content: space-evenly;
    gap: 1rem;

    @include media(phone) {
        flex-direction: column;
    }

    .footer-about {
        .shitpunks-logo-footer {
            width: 5rem;
            height: 5rem;
            margin-right: 1rem;
        }

        display: flex;

        @include media(phone) {
            flex-direction: column;
            align-items: center;
        }
    }
    
    .footer-social {
        display: flex;
        flex-direction: column;
        
        @include media(phone) {
            align-items: center;
        }

        .social-icons {
            justify-content: center;
            padding-right: 0;
        }
    }
}

.footer-contract {
    margin-top: 1rem;
    font-size: $font-size-small;
}

.footer-header {
    font-size: $font-size-small;
    white-space: nowrap;
}

.footer-text {
    font-size: $font-size-small;
    max-width: 50rem;

    @include media(phone) {
        margin-top: 1rem;
        margin-bottom: 1rem;
        text-align: center;
        max-width: 30rem;
    }
}

.footer-copyright {
    font-size: $font-size-xsmall;
    width: 100%;
    text-align: center;
    margin-top: 2rem;
}