.nft-calendar-intro {    
    margin-bottom: 2rem;

    .nft-calendar-addtoken {
        margin-bottom: 1rem;
    }
}

.nft-calendar-disclaimer {
    display: flex;
    align-items: center;
    i {
        color: $color-primary;
        padding-right: 1rem;
    }
    
    padding-left: 1rem;
    font-size: $font-size-medium;
    margin-bottom: 2rem;
    
    border: 2px solid $color-primary;
    justify-content: center;
    padding: 1rem;
    border-radius: $border-radius-small;
}

.nft-calendar-links {
    display: flex;
    gap: .5rem;
    font-size: $font-size-medium;
    a {
        &:hover {
            cursor: pointer;
            color: $color-white;
        }
    }
}

.nft-calendar-image {
    img {
        width: 10rem;
        max-height: 10rem;
        margin-bottom: .5rem;
        border-radius: $border-radius-small;    
    }
}

.nft-calendar-date {
    font-size: $font-size-large;
    font-family: $font-secondary;
    color: $color-primary;   
    padding-bottom: .5rem;
}

.nft-calendar-title {
    font-weight: 600;
    a {
        color: $color-white-bright;
    }
    
}

.nft-calendar-token {
    white-space: nowrap;

    .nft-calendar-token-price {
        display: flex;
        gap: .5rem;
        align-items: center;
        .bnb-icon {
            height: 1.6rem;
        }
    }
}

.nft-calendar-description {
    font-family: "Raleway";
    font-style: italic;
    margin-bottom: 1.5rem;
}

.nft-calendar-date-row {
    &:not(:first-child) {
        .nft-calendar-date {
            padding-top: 3rem;
        }
    }
}

.nft-calendar-grid {
    display: grid;
    grid-template-columns: 10rem 0.5fr 0.1fr 1fr;
    gap: 0.5rem 1rem;

    .nft-calendar-date-row {
        grid-column: 1 / -1;
    }

    
    @include media(phone) {
        grid-template-columns: 10rem 1fr 0.1fr;

        .nft-calendar-image {
            grid-row: span 2;

            img {
                border-radius: $border-radius-small;
            }
        }

        .nft-calendar-description {
            grid-column: 2 / -1;
            margin-bottom: 2rem;
        }
    }
}

