
.react-datepicker {
    background-color: $color-grey-dark-4;
    color: $color-white;
    font-size: $font-size-medium;
    font-family: $font-secondary;
    border: 1px solid $color-primary;

    .react-datepicker__triangle {
        display: none;
    }

    .react-datepicker__header {
        background-color: $color-primary;
    }

    .react-datepicker__current-month {
        font-size: $font-size-medium;
    }

    .react-datepicker__day-name {
        
        padding: 1rem 1.3rem;
        width: 4rem;
    }

    .react-datepicker__day {
        color: $color-white;
        padding: 1rem 1.3rem;
        
        width: 4rem;
        line-height: initial;
        
        &:hover {
            background: $color-primary;
        }
    }
}

.nftdrop-form {
    display: grid;

    #name {
        display: none !important;
    }

    label {
        margin-top: 1rem;
        color: $color-grey-light-2;

        span {
            font-family: $font-italic;
            font-style: italic;
            margin-left: 1rem;
        }
    }

    input[type=text] {
        line-height: 3rem;
        background: black;
        border: 1px solid $color-grey-dark-2;
        border-radius: $border-radius-small;
        color: $color-white;
        padding: .5rem 1rem;
        outline: none;
    }

    textarea {
        line-height: 3rem;
        background: black;
        border: 1px solid $color-grey-dark-2;
        border-radius: $border-radius-small;
        color: $color-white;
        padding: .5rem 1rem;
        outline: none;
    }

   
    .image-upload-component {
        display: flex;
    }

    .image-preview {
        margin: 1rem;
        height: 15rem;
        border-radius: $border-radius-small;
    }

    .two-columns {
        display: grid;
        grid-template-columns: 50% 50%;

        :first-child {
            margin-right: 1rem;
        }
    }

    .nftdrop-validation-error {
        color: $color-danger;
    }
}