// 1em = 16px
@mixin media($breakpoint) {
    @if $breakpoint == mini-phone {
        @media only screen and (max-width: 22em) { @content };
    }
    @if $breakpoint == phone {
        //@media only screen and (max-width: 48em) { @content };      //768px
        @media only screen and (max-width: 51em) { @content };      //816px
    }
    @if $breakpoint == tablet {
        @media only screen and (max-width: 64em) { @content };      //1024px
    }
    @if $breakpoint == big-desktop {
        //@media only screen and (min-width: 112.5em) { @content };   //1800px
        @media only screen and (min-width: 100em) { @content };   //1600px
    }
}
