.marketplace-stats {
    position: relative;

    .marketstat-diff {
        margin-left: .3rem;
        font-size: $font-size-small;

        &--positive {
            color: $color-green;
        }

        &--negative {
            color: $color-danger;
        }

        &--neutral {
            color: $color-grey-dark-1;
        }
    }
}

.marketplace-stats-boxes {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    gap: 1rem;

    .bnb-icon {
        height: 1.5rem;
        margin-right: 0.4rem;
    }

    @include media(mini-phone) {
        font-size: x-small !important;

        .marketplace-stat-label {
            font-size: x-small !important;
        }
    };
}

.extra-info {
    display: none;
    background: rgba($color-grey-dark-4, .95);
    border: 1px solid $color-grey-dark-3;
    border-radius: $border-radius-small;

    padding: 1rem;
    position: absolute;
    left: 0;
    top: calc(100% + .5rem);
    z-index: 100;
    width: 100%;
}

.marketplace-stat-box {
    border: 2px solid $color-primary-dark;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    
    border-radius: $border-radius-small;

    padding: 1rem;
    width: 20rem;
    max-width: 20rem;

    .marketplace-stat-price {
        display: flex;
        align-items: center;
    }

    .marketplace-stat-label {
        color: $color-primary;
        text-transform: uppercase;
        font-size: small;
        margin-bottom: .5rem;
        white-space: nowrap;
    }

    .marketplace-stat-value {
        display: flex;
        align-items: center;
    }

    font-family: $font-secondary;

    &:hover .extra-info {
        display: block;
    }
    
}

.marketplace-hover-stats {
    font-family: $font-secondary;
    font-size: $font-size-small;
}