#popup-root > .popup-overlay {
  background-color: rgba(0, 0, 0, 0.8);
}

#popup-root > .popup-overlay ~ .popup-overlay {
  background-color: transparent !important;
}

.popup-overlay {
  z-index: 999 !important;
}

.popup-content {
  background: rgba($color-grey-dark-4,.7);
  padding: 2rem;
  border: 2px solid $color-grey-dark-4;
  border-radius: $border-radius-small;
  max-width: 78rem;

  @include media(phone) {
    max-width: 90vw;
  }

  .content {
    overflow: auto;
  }
}

.popup-arrow {
  display: none;
}
