$color-primary: rgb(255, 122, 0);
$color-primary-light: lighten(rgb(255, 122, 0), 20%);
$color-primary-dark: darken(rgb(255, 122, 0), 20%);
$color-primary-darker: darken(rgb(255, 122, 0), 40%);

$color-secondary-light: #38B;
$color-secondary-dark: #123;

$color-grey-light-1: #f7f7f7;
$color-grey-light-2: #eee;

$color-grey-dark-1: #999;
$color-grey-dark-2: #666;
$color-grey-dark-3: #333;
//$color-grey-dark-4: #27242c;
$color-grey-dark-4: #211f20;


$color-white: darken(#f9f5f1, 5%);
$color-white-bright: #f9f5f1;
//$color-white: #ddd;
//$color-white: #f9f5f1;
//$color-black: #181719;
$color-black: #191718;

$color-text-primary: darken($color-white, 30%);
$color-text-secondary: darken($color-white, 30%);

//$color-danger: #ED2D00;
$color-green: #13BA22;

$color-danger-dark: #C60E00;
$color-danger-light: #FF4739;
$color-danger: #FF1300;

