@keyframes punk-row {
    0% {
        //transform: translate(calc(7 * 100%), -1rem);
        //transform: translate(0, 100rem);
        //opacity: 0;
        transform: translateY(-.5rem);
    }

    80% {
        transform: translateY(-.5rem);
    }

    90% {
        transform: translateY(.5rem);
    }
    
    100% {
        //transform: translateX(0);
        //opacity: 1;
        transform: translateY(-.5rem);
    }
}


@keyframes punk-head-nod {
    0% {
        transform: translateY(0rem);
    }

    5% {
        transform: translateY(.5rem);
    }

    10% {
        transform: translateY(.0rem);
    }
    
    25% {
        transform: translateY(0rem);
    }


    100% {
        transform: translateY(0rem);
    }
}


$shitpunk-row-animation-delay: 1s;

.shitpunks-row--outer {
    position: relative;
}

.punk-row-card:nth-child(2) img {
        animation: punk-head-nod 12s ease;
        animation-delay: 8s;
        animation-iteration-count: infinite;   
}

.punk-row-card:nth-child(4) img {
    animation: punk-head-nod 12s ease;
    animation-delay: 5s;
    animation-iteration-count: infinite;

}

.punk-row-card:nth-child(6) img {
    animation: punk-head-nod 13s ease;
    animation-delay: 2s;
    animation-iteration-count: infinite;

}




.shitpunks-row {
    display: grid;
    grid-template-columns: repeat(7, 1fr);

    background: $color-primary;
    background: linear-gradient(180deg, #e53800, #fbb41e);
    background: linear-gradient(
180deg,#e53800,#f93c02 22.4%,#f95f09 42.19%,#f88613 68.75%,#fbb41e)!important;
    margin-top: 2rem;
    min-height: calc(100vw / 7);

    @for $i from 1 through 7 {
        .punk-row-card:nth-child(#{$i}n) {
            
            transform: translateY(#{1 + $i * -1}rem);
            
            img {
                //opacity: 0;
                //animation: punk-row 5s ease;
                //animation-fill-mode: forwards;    
                //animation-delay: #{ (($i * 3) % 7) * 0.7}s;
                //animation-iteration-count: infinite;
            }
            
        }
    }

    .punk-row-card img {
        
        //animation-delay: 1s;
    }
/*
    .punk-row-card img {
        clip-path: polygon(
            0 1rem,
            100% 0rem,
            100% calc(100% - 1rem),
            0 calc(100%)
        );
    }
    */

    padding-top: 6rem;
    //margin-bottom: -7rem;
    /*
    clip-path: polygon(
        0 7rem,
        100% 0rem,
        100% calc(100% - 7rem),
        0 calc(100%)
    );


    @include media(tablet) {
        padding-top: 4rem;
        clip-path: polygon(
            0 5rem,
            100% 0rem,
            100% calc(100% - 5rem),
            0 100%
        );
    }

    @include media(phone) {
        padding-top: 3rem;
        clip-path: polygon(
            0 4rem,
            100% 0rem,
            100% calc(100% - 4rem),
            0 100%
        );
    }
    */

    @include media(tablet) {
        grid-template-columns: repeat(5, 1fr);
        .punk-row-card:nth-of-type(6),
        .punk-row-card:nth-of-type(7) {
            display: none;
        }
    }

    @include media(phone) {
        grid-template-columns: repeat(4, 1fr);
        .punk-row-card:nth-of-type(5),
        .punk-row-card:nth-of-type(6),
        .punk-row-card:nth-of-type(7) {
            display: none;
        }
    }

    width: 100%;

    a {
        position: relative;
        display: flex;
        width: 100%;
    }

    img {
        width: 100%;
    }

    .punk-info-wrapper {
        position: absolute;
        padding: .2rem .5rem;
        width: 100%;
        opacity: 0;

        transition: all .5s ease;
  
        bottom: -2.8rem;
        display: flex;
        justify-content: center;
        width: 100%;

        .punk-info {
            font-family: $font-secondary;
            font-size: $font-size-small;
            text-align: center;
            background: #111;
            width: max-content;
            border: 1px solid #ff7a01;
            border-radius: 5px;
            padding: 0.5rem .8rem;
            opacity: .75 !important;
        }
    }

    .punk-row-card:hover {
        .punk-info-wrapper {
            opacity: 1;
            bottom: -5rem;
        }
    }


    .punk-row-card {
        transition: all .2s ease;
    }

    &:hover .punk-row-card {
        opacity: .75;
    }

    .punk-row-card:hover {
        opacity: 1;
    }
}

.shitpunks-row-triangle-top {
    svg {
        position: absolute;
        top: 0;         
        width: 100%;   
    }

    polygon {
        fill:blue;
    }
}

.shitpunks-row-triangle-bottom {
  
    svg {
        position: absolute;
        bottom: 0;
        width: 100%;
        
    }
    polygon {
        fill:blue;
    }
}



@keyframes punk-head-nod2 {
    0% {
        transform: translateY(0rem);
    }

    1% {
        transform: translateY(.05rem);
    }

    2% {
        transform: translateY(.0rem);
    }
    
    100% {
        transform: translateY(0rem);
    }
}

@keyframes punk-speech-bubble2 {
    0% {
        transform: translateY(0rem);
        opacity: 0;
    }

    2% {
        transform: translateY(.1rem);
        opacity: 1;
    }

    8% {
        transform: translateY(.0rem);
        opacity: 1;
    }
    
    10% {
        transform: translateY(0rem);
        opacity: 0;
    }


    100% {
        transform: translateY(0rem);
    }
}


.punk-row-background {
    background: linear-gradient(178deg,
        #e53800,
        #e53800, 50%,
        #fbb41d 51%,
        #fbb41d
    )!important;
}



$initial-speech-delay: 3;
$time-between-speech: 5;
$total-animation-time: #{$time-between-speech * 7 + $initial-speech-delay}s;


.punk-row-image2 {
    animation: punk-head-nod2 38s ease;
    //animation-delay: 8s;
    animation-iteration-count: infinite;      
}

.punk-row-2 {
    position: relative;

}

@for $i from 1 through 7 {
    .speech-bubble--punk#{$i} {
        left: calc(#{$i - 1} * 100% / 7);
        text-align: center;
        bottom: calc(60% + #{$i} * 4% );
        
       
        //opacity: 1 !important;

        animation: punk-speech-bubble2 38s ease;
        animation-iteration-count: infinite;     
    }
}

.speech-bubble--punk7 {
    left: initial;
    right: 0;
}

.speech-bubble--punk3,.punk-row-image2--3 { animation-delay: 3s ; }
.speech-bubble--punk6,.punk-row-image2--6 { animation-delay: 8s ; }
.speech-bubble--punk4,.punk-row-image2--4 { animation-delay: 13s ; }
.speech-bubble--punk1,.punk-row-image2--1 { animation-delay: 18s; }
.speech-bubble--punk5,.punk-row-image2--5 { animation-delay: 23s; }
.speech-bubble--punk2,.punk-row-image2--2 { animation-delay: 28s; }
.speech-bubble--punk7,.punk-row-image2--7 { animation-delay: 33s; }

@keyframes punk-image-in {
    0% {
        transform: translateX(10rem);
        opacity: 0;
    }
    
    100% {
        transform: translateX(0rem);
        opacity: 1;
    }
}

@for $i from 1 through 7 {
    .punk-image-wrapper--#{$i} {
        position: absolute;
        animation: punk-image-in #{.4 + $i*0.1}s ease;
        opacity: 0;
        animation-delay: 0s !important;
        animation-fill-mode: forwards;   
    }
}

@keyframes punk-letter-in {
    0% {
        transform: translateX(100rem);
        opacity: 0;
    }
    
    20% {
        transform: translateX(0rem);
        opacity: 1;
    }

    30% { color: #fbb41e;    }
    35% { color: $color-white; }
    40% { color: #fbb41e;    }

    95% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

@keyframes punk-row-fadein {
    0% {
        opacity: 0.6;
    }
    
    50% {
        opacity: .6;
    }

    100% {
        opacity: 1;
    }
}

