.punk-row-clipped {
    opacity: 1;
    //animation: punk-row-fadein 3s linear;
}

.punk-of-the-day-text {
    //font-size: calc(4vw);
    font-size: 5vw;

    @include media(big-desktop) {
        font-size: 5rem;
    }

    position: absolute;
    top: 0;
    display: flex;
    margin-left: 2rem;
    justify-content: flex-start;
    align-items: flex-start;
    width: calc(100% - 5rem);
    height: 100%;
    transform: skewY(-3deg);
    text-transform: uppercase;
    font-weight: 700;
    overflow: hidden;
    pointer-events: none;

    span {
       // min-width: 2rem;
    }

    @for $i from 1 through 16 {
        .letter--#{ $i - 1} {
            //opacity: 1;
            opacity: 0;
            white-space: pre;
            color: #fbb41e;
            animation: punk-letter-in #{2.4 + $i*0.05}s ease;
            animation-delay: 0.1s;
            animation-iteration-count: 1;
        }
    }
}

.section-punks-row {
}

.speech-bubbles {
    overflow-x: clip;
    position: absolute;
    top: 0;
    //background: red;
    width: 100%;
    height: 100%;
    pointer-events: none;
}

.punk-row-image2 {
    position: relative;
    &:hover {

        &::after {
            content: "";
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            background: $color-white;;
            opacity: .3;
        }
    }
}

@keyframes punk-image-hover {
    0% {
        transform: translateY(0rem);
    }

    50% {
        transform: translateY(-.05rem);
    }

    100% {
        transform: translateY(.0rem);
    }
}

.punk-image-wrapper-outer {
    .punk-image-overlay {
        display: none;
    }

    &:hover {
        .punk-image-overlay {
            display: block;
            opacity: .2;
            cursor: pointer;
            pointer-events: none;
            
        }

        .punk-image-rank {
            display: block;
        }

        animation: punk-image-hover 1s ease;
    }
}



@keyframes gradient-in {
    0% {
        transform: translateX(100%);
        height: 50%;
        opacity: 0;
    }

    0% {
        transform: translateX(90%);
        opacity: 1;
        height: 100%;
    }
    
    100% {
        transform: translateX(0), scaleY(100%);
        opacity: 1;
    }

}

.punk-row-gradient-background {
    animation: gradient-in 1s ease;
    opacity: 0;
    //animation-delay: 0.4s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

.punk-image-wrapper-outer {
    //opacity: .1;
}

.punk-image-rank {
    position: absolute;
    top: 0;
    left: 0;
    display: none;
    transform: skewY(-3deg)
}

.section-punks-row--loading {
    width: 100%;
    //min-height: calc(100vw / 7 + 4vw*10/7);
    
    height: calc(100vw / 7);

    @include media(big-desktop) {
        height: calc(#{$app-max-width-big-desktop} / 7)
    }

    margin-top: 5.8rem;
    transform: skewY(-4deg);
    display: flex;
    align-items: center;
    justify-content: center;
}