.diamond-hands-container {
    margin-bottom: 1rem;
    padding: 2rem;
    border-radius: 0.5rem;
    background: #222;
    border: 1px solid #333;
    display: grid;
    grid-template-columns: 1fr min-content;

    @include media(phone) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;

        button {
            margin-top: 1rem;
        }
    }
}

.diamond-hands-extra-info {
    grid-column: 1 / 3;
    margin-bottom: 1rem;

}

.dmd-button-right {
    &:after {
        content: "to the right";
        @include media(phone) {
            content: "below";
        }
    }
}

.diamond-criterias {
    .criteria {
        display: flex;
        align-items: center;
    }
    
    .fa-check-circle {
        color: $color-green;
    }

    .fa-times-circle {
        color: $color-danger;
    }

    .criteria-description {
        margin-left: .5rem;
    }
}

.read-more-toggle {
    display: inline-block;
    cursor: pointer;
    color: $color-primary;

    &:hover {
        text-decoration: underline;
    }

    .fa-caret-down {
        display: none;
    }

    ~ .read-more-contents {
        display: none;
    }

    &--visible {
        .fa-caret-right {
            display: none;
        }
        .fa-caret-down {
            display: inline-block;
        }

        ~ .read-more-contents {
            display: block;
        }
    }
}

.diamond-hands-title {
    display: inline-block;
}

.read-more-contents {
    display: none;

    &--visible {
        display: block;
    }

    border-left: .2rem solid $color-primary;
    padding-left: 1rem;
    margin: 1rem 0rem;
    margin-bottom: 2rem;
}