.button-link {
    &:hover {
        text-decoration: none;
    }
}

$border-radius-button: 1rem;

.button {
    position: relative;

    display: flex;
    cursor: pointer;
    padding: 0;
    border: none;
    background: transparent;   
    margin: .5rem;
    margin-bottom: 1rem;

    &--disabled {
        opacity: .4;
        pointer-events: none;
    }

    .button-edge {
        content: "";
        position: absolute;
        top: .5rem;
        background: darken($color-danger, 10%);
        width: 100%;
        height: 100%;
        border-radius: $border-radius-button;
       
        background: linear-gradient(-45deg, #e53800, #fbb41e);
        transition: all .2s ease;

        box-shadow: 0px 3px 4px 0px rgba(0,0,0,.4);    
    }

    .button-front {        
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: $font-secondary;
        font-size: $font-size-medium;
        white-space: nowrap;
        line-height: 4rem;
        padding: .5rem 2rem;
        display: flex;
        width: 100%;
        height: 100%;
        background: $color-black;
        color: $color-white;
        border: 2px solid $color-primary;
        border-radius: $border-radius-button;
        background: #111;
        background: linear-gradient(0deg, #111, #fbb41e);

        background: lighten(#fbb41e, 35%);
        background: linear-gradient(45deg, 
            lighten(#fbb41e, 45%), 
            lighten(#fbb41e, 35%)
        );
        background: $color-white;
        
        color: #222;
        border: none;


        z-index: 1;
        font-weight: 500;

        transition: transform .1s ease;

        border: .3rem solid $color-primary;
  
    }

    

    &--small {
        .button-front {
            line-height: initial;
        }   
    }

    &:hover {  
        .button-front {    
            transform: translate(0rem, -.2rem);
        }
    }

    &:active {
        .button-front {  
            transform: translate(0rem, .3rem);

            border-top-color: darken($color-primary, 10%);
            border-left-color: darken($color-primary, 10%);
        }
    }



    &--secondary {
        .button-edge {
            background: linear-gradient(-45deg, #01535F, #03899C);
        }
    
        .button-front {
            border-color: #03899C;
            background: #2794A3;
        }
    
        &:active .button-front {
            border-top-color: darken(#03899C, 10%);
            border-left-color: darken(#03899C, 10%);
        }  
    }

    &--danger {
        .button-edge {
            background: linear-gradient(-45deg, $color-danger-dark, $color-danger-light);
        }
    
        .button-front {
            border-color: $color-danger-light;
            background: $color-danger;
        }
    
        &:active .button-front {
            border-top-color: darken($color-danger, 10%);
            border-left-color: darken($color-danger, 10%);
        }  
    }

    &--green {
        .button-edge {
            background: linear-gradient(-45deg, darken($color-green, 10%), lighten($color-green, 10%));
        }
    
        .button-front {
            border-color: lighten($color-green, 5%);
            background: $color-green;

            font-size: $font-size-large;
            font-weight: 600;
            font-family: $font-secondary;
        }
    
        &:active .button-front {
            border-top-color: darken($color-green, 10%);
            border-left-color: darken($color-green, 10%);
        }  
    }

    &--modal-close {
        position: absolute;
        top: -2.5rem;
        right: -2rem;

        .button-front {
            border-radius: 2rem;
            padding: 0;
            height: 4rem;
            width: 4rem;
            font-size: $font-size-xlarge;
        }

        .button-edge {
            outline: .35rem solid rgba(0,0,0,.4);
            border-radius: 2rem;
            top: 0.3rem;
        }
    }

}
