.adjust-price-modal {

    .content {
        display: flex;
        flex-direction: column;
    }

    .button-ok {
        margin-top: .5rem;
    }

    h3 {
        margin-bottom: 1rem;
    }

    .price-input-container {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 1rem;

        .price-input {
            max-width: 10rem;
            input { 
                text-align: right; 
            }
        }
    }

    .error-text {
        margin-top: .5rem;
        color: $color-danger;
    } 
}
