.marketplace-info {
    display: flex;

    @include media(phone) {
        flex-direction: column;
    }

    .marketplace-description {
        max-width: 78rem;

        @include media(phone) {
            flex-direction: column;
            margin-bottom: 2rem;
        }
    }
}

.marketplace-main {
    display: flex;

    @include media(phone) {
        @media (orientation: portrait) {
            flex-direction: column;
        }
    }
}

.punk-results-container {
    width: 100%;

    .button-fetch-more {
        margin: auto;
    }
}

.punk-card-list-marketplace {
    margin-bottom: 3rem;
}

.infinite-scroll-component__outerdiv {
    width: 100%;
}

.marketplace-info {
    display: flex;
    gap: 1rem;
    margin-bottom: 2rem;

    @include media(tablet) {
        flex-direction: column;
        
    }
}

.network-delay {
    font-size: $font-size-xsmall;
    color: $color-grey-dark-1;
}