.page-menu-bar {
    //border-bottom: 2px solid $color-grey-dark-4;
    background-color: $color-black;
    display: flex;
    gap: 2rem;
    align-items: flex-end;

    position: sticky;
    top: -3px;
    height: 5rem;
    z-index: $z-index-page-menu;
    //box-shadow: $shadow-below;
    
    justify-content: center;
    margin-bottom: .8rem;

    @include media(tablet) {
        font-size: $font-size-small;
        flex-wrap: wrap;       
    } 

}

.page-menu-bar-button {
    font-size: $font-size-large;
    position: relative;
    white-space: nowrap;
    padding-bottom: .5rem;


    @include media(phone) {
        font-size: $font-size-medium;
    } 

    i {
        color: lighten($color-primary, 10%);
    }

    &:hover {
        text-decoration: none;
    }

    &::after {
        content: "";
        position: absolute;
        width: 0%;
        height: 1px;
        background-color: $color-primary;
        bottom: -.2rem;
        left: 50%;
    }

    &--active,&:hover {
        color: $color-primary;

        &::after {
            width: 100%;
            left: 0;
            transition: all .2s ease;
        }
    }

    &--disabled {
        opacity: .4;
        cursor: not-allowed;

        &:hover {
            color: inherit;
        }
    }
}