.input-container {
    position:relative;
    margin-top: 2.5rem;

    .placeholder {
        position: absolute;
        top: 0;
        left: 0;
        padding: .4rem 1rem;
        color: $color-primary;
        pointer-events: none;
        transition: all .1s ease;
    } 
}

.input {
    padding: .8rem 1.5rem;
    appearance: none;
    outline: none;
    background: none;
    border: 1px solid $color-grey-dark-2;
    border-radius: $border-radius-small;
    color: $color-white;
    width: 45rem;
    max-width: 100%;
    font-size: $font-size-medium;

    &:not(:placeholder-shown) + .placeholder,
    &:focus + .placeholder {
        top: -2.5rem;
        font-size: $font-size-small;
        color: $color-primary;
        transition: all .1s ease;
        padding-left: 0;
    }
}
