.home-description {
    max-width: 78rem;
}

.page-home {
    h1 {
        font-size: 6rem !important; 
    }


    .columns {
        display: grid;        
        grid-template-columns: 1fr .6fr;
        gap: 3rem;

        @include media(phone) {
            grid-template-columns: 1fr;
            margin-bottom: 2rem;
        }
    }

    .column-right {
        display: flex;
        justify-content: center;
        align-items: center;

        .button-marketplace {
            margin-top: 1rem;

            .button-front {
                width: max-content;    
                font-size: $font-size-large;
                width: 100%;
                white-space: nowrap;
                padding: 1rem 2rem;

                @include media(phone) {
                    font-size: $font-size-medium;
                }
            }
        }
    }
}


//.section-fw-2:nth-last-of-type(1) {
.section-punks-row:nth-last-of-type(1) {
    background: linear-gradient(180deg, $color-black, $color-grey-dark-4);
}


.section-contents {
    h1 {
        font-size: 3.5rem;
    }
}


.why-shitpunks-boxes {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 2rem;
    gap: 2rem;
    
    @include media(phone) {
        padding: 2rem;
        grid-template-columns: 1fr;
    }
}

.why-shitpunks-box {
    border: 2px solid $color-primary;
    border-radius: $border-radius-small;
    padding: 2rem;
    width: 100%;
}

.why-shitpunks-box-title {
    font-size: $font-size-large;
    color: $color-primary;
}

.why-shitpunks-box-description {
    font-size: $font-size-medium;
}

.key-event-list {
    display: flex;
    flex-direction: column;
    gap: 3rem;
    margin-bottom: 2rem;
}

.key-event {
    border-left: 2px solid $color-primary;
    padding-left: 1rem;

    .key-event-title {
        font-weight: 600;
        color: $color-primary;
    }

    .key-event-description {
        max-width: 78rem;
        font-size: $font-size-small;
    }
}

.button-key-events {
    margin: auto;
}