.punk-search-bar {
    position: sticky;
    top: 4.5rem;
    z-index: $z-index-page-search-bar;
    background: $color-black;
    box-shadow: $shadow-below;
    gap: 1rem;

    display: flex;

    justify-content: space-between;

    margin-bottom: 1rem;
    padding: 1rem 0rem;

    .toggle-filters {
        display: none;
    }

    .filter-button {
        display: none;
    }


    @include media(phone) {

            .filter-button {
                display: flex;
                align-items: center;
                position: relative;
            }
    
            .filters-button-badge {
                display: flex;
                align-items: center;
                justify-content: center;
                font-family: $font-secondary;
                font-weight: bold;
                font-size: $font-size-small;
                background: $color-white;;
                border-radius: 50%;
                border-bottom: 2px solid $color-primary;
                width: 1.75rem;
                height: 1.75rem;
                color: $color-black;
                position: absolute;
                top: -.5rem;
                right: -.5rem;
            }
    
            .toggle-filters {
                display: block;
            }

    }
    
    .select-control {
        margin: 0;
        border-radius: $border-radius-small;

        background: $color-grey-dark-4;
        //background: linear-gradient(134deg, lighten($color-grey-dark-4, 5%), $color-grey-dark-4);
        border-top: 1px solid lighten($color-grey-dark-4, 5%) !important;

        &:hover {
            background-color: $color-primary;
            .select-control__single-value {
                color: $color-white;
            }           
        }

        width: 25rem;
        //

        @include media(phone) {
            flex-grow: 1;
            min-width: 5rem;
            max-width: initial;
        }
    }
}