@keyframes example {
    from {
        transform: translateX(10rem);
        opacity: 0;
    }
    
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

.social-icons {
    width: 100%;
    height: 4rem;
    display: flex;
    justify-content: flex-end;
    padding-right: 5rem;
    gap: 1rem;
    overflow: hidden;

    a {
        color: $color-white;
        font-size: 2.5rem;
        opacity: 0;
    }

    a:hover {
        color: $color-primary;
    }

    .social-icon--animate {
        animation: example .3s ease-out;
        animation-fill-mode: forwards;
    }    
}