.punk-filters {
    min-width: 22rem;
    position: sticky;
    top: 5rem;
    z-index: $z-index-page-filters;
    height: fit-content;
    min-height: calc(100vh - 10rem);
    max-height: calc(100vh);
    overflow-y: auto;

    //overflow-y: auto;
    margin-right: 1rem;

    .filter-checkbox {
        text-transform: uppercase;
        font-size: small;
        margin-bottom: 1rem;
    }

    .toggle-filters {
        display: none;
    }

    .search-button {
        display: none;
    }

    .punk-filters-header {
        height: 5rem;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        color: $color-primary;
    }


    left: -100vw; // hack to fix flickering when switching views / viewport size

    @include media(phone) {
        //@media not screen and (device-width:717px) {
        //@media (orientation: portrait) {
            position: fixed;
            top: 0rem;
            left: 0rem;
            width: 100vw;
            height: 100vh;
            background: rgba(#111, .98);
    
            transition: all .3s ease-in-out;
            transform: translateX(-150%);

            z-index: $z-index-page-filters-window;
            
            overflow: visible;
            
            //border: 2px solid $color-primary;
            //border-radius: $border-radius-small;
            padding: 3rem;
            overflow-y: auto;
            
            .punk-filters-header {
                display: none;
            }
    
            .toggle-filters {
                display: block;
                color: $color-primary;
                font-size: $font-size-large;
                height: 4rem;
    
                button {
                    top: 1.5rem;
                    right: 3.5rem;
                }
            }
    
            .search-button {
                    
                display: flex;
                width: 100%;
                justify-content: center;
            }
       // }
       
    }
    
    &--visible {
        display: block;
        transform: translateX(0%);
    }
}
